<template>
  <section class="infofolder_section">
    <div class="bg"></div>
    <div class="title">Contents & Video</div>
    <div class="container">
      <div class="video-con">
        <iframe
          width="100%"
          height="100%"
          :src="safeUrl(video[accordion_num])"
          title="YouTube video player"
          frameborder="0"
          allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
          allowfullscreen
        ></iframe>
      </div>

      <div class="accordion">
        <div
          class="accordion-item"
          v-for="(content, index) in folds"
          :key="index"
          :id="'question' + index"
        >
          <div class="accordion-list" @click="open_text(index)">
            <div class="accordion-con">
              <span class="accordion-title">{{ content.title }}</span>
              <span class="accordion-subtitle">{{ content.subtitle }}</span>
            </div>
            <v-icon :class="'arrow' + index">mdi-arrow-right</v-icon>
          </div>
          <div :class="'text' + index">
            <p>{{ content.text }}</p>
          </div>
          <hr />
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: "InfoFolderPage",
  data() {
    return {
      folds: [
        {
          title: "Video",
          subtitle:
            "        ",
          text: "     ",
        },
        {
          title: "Video",
          subtitle:
            "        ",
          text: "     ",
        },
        {
          title: "Video",
          subtitle:
            "        ",
          text: "     ",
        },
        {
          title: "Video",
          subtitle:
            "        ",
          text: "     ",
        },
      ],
      video: [
        "https://www.youtube.com/embed/J4Fvcp_KsoQ",
        "https://www.youtube.com/embed/qNm_fVKd8kM",
        "https://www.youtube.com/embed/0FbJhm00k1Y",
        "https://www.youtube.com/embed/36ggFM3Dju8",
      ],
      accordion_num: 0,
    };
  },
  methods: {
    safeUrl(url) {
      return url;
    },
    open_text(num) {
      const text = document.querySelector(".text" + num);
      const arrow_right = document.querySelector(".arrow" + num);
      const isOpen = text.style.height === "100px";
      //   text    
      if (isOpen) {
        text.style.height = "0";
        arrow_right.style.transform = "rotate(0deg)";
      } else {
        //  text     
        if (this.accordion_num !== null) {
          //  text   
          const prevText = document.querySelector(".text" + this.accordion_num);
          const prevArrow = document.querySelector(
            ".arrow" + this.accordion_num
          );
          prevText.style.height = "0";
          prevArrow.style.transform = "rotate(0deg)";
        }
        arrow_right.style.transform = "rotate(90deg)";
        text.style.height = "100px";
        this.accordion_num = num;
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.bg {
  width: 100%;
  height: 100vh;
  position: absolute;
  background-image: url("@/assets/Main/sky.jpg");
  background-size: cover;
}
.infofolder_section {
  width: 100%;
  height: 100vh;
  display: inline-block;
  align-items: center;
  justify-content: center;
  position: relative;
  .title {
    position: relative;
    height: 35%;
    justify-content: center;
    align-items: center;
    display: flex;
    font-family: sans-serif;
    font-weight: bold;
    font-size: 5rem;
    color: white;
  }
}
.container {
  position: relative;
  width: 70vw;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  .accordion {
    width: 50%;
    height: 40vh;
    margin-left: 5%;
  }

  .video-con {
    height: 40vh;
    width: 40%;
    padding: 12px;
  }
}
.accordion-item {
  .accordion-list {
    font-size: 1.6rem;
    color: rgb(0, 0, 0);
    text-decoration: none;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1% 0;
    cursor: pointer;

    .accordion-con {
      display: flex;
      width: 100%;
      .accordion-title {
        width: 15%;
        display: flex;
        justify-content: left;
        align-items: center;
        text-align: left;
        font-family: "Pretendard";
        font-weight: 500;
        font-size: 1.5rem;
      }
      .accordion-subtitle {
        display: flex;
        align-items: center;
        font-family: "Pretendard";
        font-weight: 500;
        font-size: 0.89rem;
      }
    }
  }

  hr {
    border: 1px solid rgba(255, 255, 255, 0.5);
  }

  @for $i from 0 to 4 {
    .text#{$i} {
      height: 0px;
      margin: 12px;
      overflow: hidden;
      position: relative;
      background-color: #ffffff6b;
      transition: all 650ms;

      p {
        padding-left: 12px;
      }
    }
  }
}

.accordion-list i {
  color: #9974c9;
  padding: 0.5rem;
  transition: all 0.3s ease-in-out;
}

