<template>
  <section class="business-creator-section">
    <div class="upper-container">
      <Business />
    </div>
    <div class="low-container">
      <section class="creator-section">
        <div class="bg"></div>
        <div class="container">
          <div class="title-con">
            <span class="title">Creator Introduce</span>
          </div>
          <div class="creator-con">
            <div class="creator-left">
              <div
                :class="'card' + card.num"
                v-for="(card, index) in LTcreators"
                :key="index"
              >
                <img
                  :src="card.image"
                  style="width: 60%; height: 40%; margin-top: 60%"
                />
              </div>
            </div>
            <div class="creator-right">
              <div
                :class="'card' + card.num"
                v-for="(card, index) in RTcreators"
                :key="index"
              >
                <img
                  :src="card.image"
                  style="width: 60%; height: 40%; margin-top: 60%"
                />
              </div>
            </div>
            <div class="btn-con">
              <router-link to="/Creator">
                <div class="circle">
                  <span class="btn">More</span>
                </div>
              </router-link>
            </div>
          </div>
        </div>
      </section>
    </div>
    <div class="anim-padding" style="height: 100vh; visibility: hidden"></div>
    <InfoFolder class="info-folder" />
  </section>
</template>

<script>
import { gsap, ScrollTrigger } from "gsap/all";
import Business from "@/components/home/Business.vue";
import InfoFolder from "@/components/home/InfoFolder.vue";
export default {
  components: {
    Business,
    InfoFolder,
  },
  data() {
    return {
      LTcreators: [
        { name: "", image: require("@/assets/Creator/logo.png"), num: 0 },
        { name: "", image: require("@/assets/Creator/logo.png"), num: 2 },
        { name: "", image: require("@/assets/Creator/logo.png"), num: 4 },
      ],
      RTcreators: [
        { name: "", image: require("@/assets/Creator/logo.png"), num: 1 },
        { name: "", image: require("@/assets/Creator/logo.png"), num: 3 },
        { name: "", image: require("@/assets/Creator/logo.png"), num: 5 },
      ],
    };
  },
  mounted() {
    gsap.registerPlugin(ScrollTrigger);
    //    creator  
    const SizeUpTimeline = gsap.timeline({
      scrollTrigger: {
        trigger: ".upper-container",
        pin: true,
        start: "top top",
        end: "+=400%",
        scrub: 1,
        onLeaveBack: () => {},
        onUpdate: (self) => {
          if (self.direction === -1 && self.progress === 0) {
            gsap.set(".low-container", { position: "fixed", top: 0 });
          } else if (self.direction === 1 && self.progress === 1) {
            gsap.set(".low-container", { position: "fixed", top: 0 });
          } else if (self.direction === 1 && self.progress !== 1) {
            gsap.set(".low-container", { position: "fixed", top: 0 });
          } else if (self.direction === -1 && self.progress !== 1) {
            gsap.set(".low-container", { position: "fixed", top: 0 });
          }
        },
        onComplete: () => {},
      },
      defaults: {
        ease: "none",
      },
    });
    SizeUpTimeline.to(
      ".low-container",
      {
        scale: 1,
        opacity: 1,
        borderRadius: 0,
      },
      "start+=.1"
    );
    SizeUpTimeline.to(
      ".creator-con",
      {
        top: "-63.86%",
      },

      "start+=.7"
    );

    //   
    const scrollPadding = gsap.timeline({
      scrollTrigger: {
        trigger: ".anim-padding",
        start: "top bottom",
        end: "bottom bottom",
        scrub: 1,
        onUpdate: (self) => {
          if (self.direction === -1 && self.progress === 0) {
            gsap.set(".low-container", { position: "fixed", top: 0 });
          } else if (self.direction === 1 && self.progress === 1) {
            gsap.set(".low-container", {
              position: "absolute",
              top: "71.43%",
            });
          } else if (self.direction === 1 && self.progress !== 1) {
            gsap.set(".low-container", { position: "fixed", top: 0 });
          } else if (self.direction === -1 && self.progress !== 1) {
            gsap.set(".low-container", { position: "fixed", top: 0 });
          }
        },
      },
      defaults: {
        ease: "none",
      },
    });

    scrollPadding.to(".anim-padding", { display: "block" });
  },
};
</script>

<style lang="scss" scoped>
.business-creator-section {
  position: relative;
}
.low-container {
  position: fixed;
  transform: scale(0);
  top: 0;
  width: 100vw;
  height: 100vh;
  border-radius: 300px;
  overflow: hidden;
}
.creator-section {
  height: 350vh;
  width: 100%;
  position: absolute;
}
.bg {
  width: 100%;
  height: 350vh;
  z-index: -1;
  position: absolute;
  background-image: url("@/assets/Main/sky.jpg");
  background-size: cover;
}
.container {
  position: relative;
  display: flex;
  justify-content: center;
  text-align: center;
  height: 100%;

  .title-con {
    position: absolute;
    height: 35vh;
    z-index: 1;
    .title {
      position: sticky;
      display: grid;
      align-content: center;
      height: 100%;
      top: 3%;
      font-family: "Pretendard";
      font-weight: bold;
      font-size: 7rem;
      color: white;
    }
  }
  .creator-con {
    width: 70%;
    height: 100%;
    display: flex;
    justify-content: center;
    position: absolute;

    .creator-left {
      margin-top: 30%;
      justify-content: center;
      width: 50%;
    }
    .creator-right {
      margin-top: 0%;
      justify-content: center;
      width: 50%;
      z-index: 5;
    }
  }
  .btn-con {
    position: absolute;
    display: flex;
    justify-content: center;
    bottom: 10%;
    width: 30%;
    z-index: 5;
    .circle {
      width: 15vw;
      height: 5vw;
      display: flex;
      border-radius: 35px;
      align-items: center;
      justify-content: center;
      border: solid 3px white;
      transition: all 0.3s ease-in-out;
    }
    .btn {
      font-family: sans-serif;
      font-size: 2rem;
      font-weight: bold;
      color: rgb(255, 255, 255);
      transition: all 0.2s ease-in-out;
    }

    .circle:hover {
      background-color: white;

      .btn {
        color: black;
      }
    }
  }
}
a {
  text-decoration: none;
}
</style>
