<template>
  <section class="business-section">
    <div class="bg"></div>
    <div class="container">
      <div class="section-text"><span class="font">Businesss</span></div>
      <div class="card-container">
        <div
          :class="'card' + index"
          v-for="(card, index) in cards"
          :key="index"
          :style="
            'background-image: url(' + card.img + '); background-size: cover;'
          "
        >
          <span class="sub-title">
            {{ card.sub_title }}
          </span>
          <span class="title">
            {{ card.title }}
          </span>
          <!-- <img :src="card.img" class="card-img" /> -->
        </div>
      </div>
      <div class="btn-container">
        <router-link to="/Business">
          <div class="circle">
            <span class="btn">More</span>
          </div>
        </router-link>
      </div>
    </div>
  </section>
</template>

<script>
import { gsap, ScrollTrigger } from "gsap/all";
export default {
  name: "BusinessPage",
  data() {
    return {
      cards: [
        {
          title: "Title1",
          sub_title: "SubTitle1",
          img: require("./../../assets/Main/sky.jpg"),
        },
        {
          title: "Title2",
          sub_title: "SubTitle2",
          img: require("./../../assets/Main/sky.jpg"),
        },
        {
          title: "Title3",
          sub_title: "SubTitle3",
          img: require("./../../assets/Main/sky.jpg"),
        },
        {
          title: "Title4",
          sub_title: "SubTitle4",
          img: require("./../../assets/Main/sky.jpg"),
        },
      ],
    };
  },
  mounted() {
    gsap.registerPlugin(ScrollTrigger);

    const business = gsap.timeline({ paused: true });

    business
      .to(".section-text", { duration: 1, top: 0, opacity: 1 }, "start")
      .to(".card0", { duration: 1, left: 0, opacity: 1 }, "start+=.5")
      .to(".card1", { duration: 1.2, bottom: 0, opacity: 1 }, "start+=.5")
      .to(".card2", { duration: 1.4, bottom: 0, opacity: 1 }, "start+=.5")
      .to(".card3", { duration: 1.6, left: 0, opacity: 1 }, "start+=.5")
      .to(".circle", { duration: 1, opacity: 1 }, "start+=1.5");

    gsap.timeline({
      scrollTrigger: {
        trigger: ".second-con ",
        start: "top center",
        end: "+=100%",
        scrub: true,
        animation: business,
        onEnter: () => {
          business.play();
        },
        onLeave: () => {},
      },
    });
  },
};
</script>

<style lang="scss" scoped>
.business-section {
  height: 100vh;
}
.bg {
  width: 100%;
  height: 100vh;
  z-index: -1;
  position: absolute;
  background-image: url("@/assets/Main/sky.jpg");
  background-size: cover;
}
.container {
  height: 100vh;
  .section-text {
    position: relative;
    display: flex;
    width: 80%;
    margin: auto;
    padding-top: 2%;
    justify-content: left;
    top: 50px;
    opacity: 0;
    .font {
      font-family: "Pretendard";
      font-weight: bold;
      font-size: 10rem;
      color: white;
    }
  }

  .card-container {
    display: flex;
    height: 40%;
    width: 60%;
    margin: auto;
    justify-content: center;
    @for $i from 0 through 3 {
      .card#{$i} {
        width: 20%;
        height: 70%; //  
        margin: 12px;
        margin-top: #{if($i == 0 or $i == 2, "30px", "80px")};
        overflow: hidden;
        border-radius: 35px 0px 35px 35px;
        box-shadow: 1px 1px 5px 0px gray;
        background-color: white;
        color: white;
        opacity: 0;
        position: relative;
        left: #{if($i == 0, "-10%", if($i == 3, "10%", ""))};
        bottom: #{if($i == 1, "-10%", if($i == 2, "10%", ""))};
        .title {
          display: flex;
          padding: 12px;
          justify-content: left;
        }
        .sub-title {
          display: flex;
          padding: 12px;
          justify-content: left;
        }

        // .card-img {
        //   width: 100%;
        //   height: 100%;
        //   border-radius: 0px 0px 35px 35px;
        //   background-size: cover;
        // }
      }
    }
  }

  .btn-container {
    position: relative;
    display: flex;
    justify-content: center;
    width: 100%;
    .circle {
      width: 7vw;
      height: 7vw;
      display: flex;
      opacity: 0;
      text-align: center;
      align-items: center;
      justify-content: center;
      border-radius: 100%;
      margin-top: 40px;
      box-shadow: 1px 1px 5px 0px gray;
      background-color: white;
    }
    .btn {
      font-family: sans-serif;
      font-size: 1.125rem;
      color: black;
      font-weight: bold;
    }
  }
}
p {
  margin: 0;
}
a {
  text-decoration: none;
}
// PC
@media screen and (min-width: 1080px) {
}

// 
@media screen and (min-width: 760px) and (max-width: 1080px) {
}

// 
@media screen and (min-width: 640px) and (max-width: 759px) {
}

// 
@media screen and (min-width: 320px) and (max-width: 639px) {
}
</style>
